<template>
  <hr />
  <average-summary
    :totalUnit="total.unit"
    :averagePrice="average.price"
  ></average-summary>
  <hr />
  <average-item-header></average-item-header>

  <!-- <span>{{ item.unit }}</span> -->
  <average-item
    :unit="item.unit"
    :price="item.price"
    :total="item.total"
    :index="index"
    :size="items.length"
    @action="onAction"
    @update="onUpdate"
    v-for="(item, index) in items"
    :key="index"
  ></average-item>
</template>
<script lang="ts">
import { defineComponent } from 'vue';

import AverageItem from '@/components/AverageItem.vue';
import AverageItemHeader from '@/components/AverageItemHeader.vue';
import AverageSummary from '@/components/AverageSummary.vue';
import AverageItemModel from '@/models/AverageItemModel';
import { config, Configuration } from '@/util/config';

export default defineComponent({
  name: 'average-item-list',
  components: {
    AverageItem,
    AverageItemHeader,
    AverageSummary,
  },
  watch: {
    items: {
      deep: true,
      handler() {
        this.calculateAverage();
      },
    },
  },
  data() {
    return {
      items: [] as AverageItemModel[],
      average: {
        unit: 0,
        price: 0,
      },
      total: {
        unit: 0,
        price: 0,
      },
      config: config as Configuration,
    };
  },
  methods: {
    onAction(value: boolean, index: number) {
      if (value) {
        const averageItemModel: AverageItemModel = new AverageItemModel();
        this.items.splice(0, 0, averageItemModel);
      } else {
        const item = this.items[index];

        if (item) {
          this.items.splice(index, 1);
        }
      }
    },
    onUpdate(index: number, value: AverageItemModel) {
      const item = this.items[index];

      if (item) {
        item.unit = value.unit;
        item.price = value.price;
        item.total = value.total;
      }
    },
    calculateAverage() {
      const size: number = this.items.length;

      const tmpItems = this.items.filter((item: AverageItemModel) => item && item['unit']);

      this.total.unit = tmpItems.reduce((a: number, b: AverageItemModel) => a + (b.unit || 0), 0);
      this.total.price = tmpItems.reduce((a: number, b: AverageItemModel) => a + (b.price || 0), 0);
      this.average.unit = this.total.unit / size;
      this.average.price = this.total.price / size;
    },
  },
  mounted() {
    const averageItemModel: AverageItemModel = new AverageItemModel();

    this.items = [averageItemModel];
  },
});
</script>
<style scoped lang="scss"></style>
