<template>
  <div class="p-fluid p-grid p-justify-center p-ai-center vertical-container">
    <div class="p-col-1">
      {{ index + 1 }}
    </div>
    <div class="p-col-3">
      <InputNumber
        v-model="tmpUnit"
        mode="decimal"
        :useGrouping="false"
        :minFractionDigits="config.minFractionDigits"
        :maxFractionDigits="config.maxFractionDigits"
      ></InputNumber>
    </div>
    <div class="p-col-1">*</div>
    <div class="p-col-3">
      <InputNumber
        v-model="tmpPrice"
        mode="decimal"
        :useGrouping="false"
        :minFractionDigits="config.minFractionDigits"
        :maxFractionDigits="config.maxFractionDigits"
      ></InputNumber>
    </div>
    <div class="p-col-1">=</div>
    <div class="p-col-2  p-text-left">
      <div class="p-pl-2">
        <span>{{ tmpTotal.toFixed(config.maxFractionDigits) }}</span>
      </div>
    </div>
    <div class="p-col-1">
      <i
        class="pi pi-plus cursor-pointer"
        @click.stop.prevent="addItem"
        v-if="index === 0"
      ></i>
      <i
        class="pi pi-times cursor-pointer"
        @click.stop.prevent="removeItem(index)"
        v-else
      ></i>
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent } from 'vue';
import InputNumber from 'primevue/inputnumber';
import AverageItemModel from '@/models/AverageItemModel';
import { config, Configuration } from '@/util/config';

export default defineComponent({
  name: 'average-item',
  components: {
    InputNumber,
  },
  props: {
    unit: {
      type: Number,
      required: true,
    },
    price: {
      type: Number,
      required: true,
    },
    total: {
      type: Number,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
    size: {
      type: Number,
      required: true,
    },
  },
  emits: ['action', 'update'],
  data() {
    return {
      tmpUnit: this.unit,
      tmpPrice: this.price,
      tmpTotal: this.total,
      config: config as Configuration,
    };
  },
  methods: {
    getTotal() {
      return this.tmpUnit * this.tmpPrice;
    },
    addItem() {
      this.$emit('action', true);
    },
    removeItem(index: number) {
      this.$emit('action', false, index);
    },
    updateItem() {
      const averageItemModel: AverageItemModel = new AverageItemModel();
      averageItemModel.unit = this.tmpUnit;
      averageItemModel.price = this.tmpPrice;
      averageItemModel.total = this.tmpTotal;

      this.$emit('update', this.index, averageItemModel);
    },
  },
  watch: {
    unit: {
      handler() {
        this.tmpUnit = this.unit;
      },
    },
    price: {
      handler() {
        this.tmpPrice = this.price;
      },
    },
    total: {
      handler() {
        this.tmpTotal = this.total;
      },
    },
    tmpUnit: {
      handler() {
        this.tmpTotal = this.getTotal();
        this.updateItem();
      },
    },
    tmpPrice: {
      handler() {
        this.tmpTotal = this.getTotal();
        this.updateItem();
      },
    },
  },
});
</script>
